import React, { useEffect, useState, useContext } from 'react';
import { useLocalStorage } from 'react-use';
// import { Link } from "react-router-dom";
import Skeleton from 'react-loading-skeleton';
import { UsuarioDataContext } from 'context/usuarioContext';
import alertaContext from 'context/alertas/alertaContext';

import axios from 'axios';
import Carrera from 'components/carrera/carrera';
import Header from 'components/header/header';

import getUuidDevice from 'components/uuidevice';
// import tid_btn from 'img/tid-btn.svg';

function Home({dni, openMenu, setOpenMenu}) {

    const {dataUsuario, remoteConstants} = useContext(UsuarioDataContext);
    const { mostrarAlerta } = useContext(alertaContext);

    const [dataCarreras, setDataCarreras] = useState(null);
    const [loading, setLoading] = useState(true);   

    //const [lastAccessTids, setLasAccessTids] = useState([]);

    const [uuidDevice] = useLocalStorage('uuidDevice', getUuidDevice());


    useEffect(() => { 
        if(Object.keys(dataUsuario).length > 0 && dni && !dataCarreras){
            const fetchTids = async () => {

                setLoading(true);

                const data = { dni, uuidDevice };

                try {
                    const result = await axios.post(`${remoteConstants.API_IES}get_tids.php`, data);

                    console.log(result);               
        
                    if(result.data.r === 0){
                        mostrarAlerta(result.data.text, "Error");
                        setLoading(false);
                        return false;
                    }
                    
                    console.log(result.data.carreras)
                    if(result.data.carreras){
                        setDataCarreras({
                                alumno: result.data.carreras,
                                moderador: result.data.carrerasModerador,
                            });
                    }
        
                }catch (error) {
                    mostrarAlerta(error, "Error");
                }
                setLoading(false);
            }
            fetchTids();
        }
        // eslint-disable-next-line
    }, [dni, dataUsuario]);

    if(Object.keys(dataUsuario).length < 1) return null;
    
    return (
        <div className="home">

            <Header 
                titulo={!loading ? '' : 'Cargando datos...'} 
                avatarData = {dataUsuario}
                utilesVisible={false}
                paginaRef={undefined}
                loading={loading}
                setOpenMenu={setOpenMenu} 
                openMenu={openMenu}
                imagesLoaded = {false}
                isTid = {false}
                showUtils={false}
                setOpenUtils={undefined}
                openUtils={false}
                showPreguntas={false}
                setOpenPreguntas={undefined}
                openPreguntas={false}
                sendAction = {undefined}
            />

            <div className="section flex nosombra pl0 pr0"> 

            { (dni && dataCarreras) ?
                <>
                    { dataCarreras.moderador.length > 0 ?
                    <>
                        <h4 className="h4HomeSub">Carreras docente</h4>
                        <div className="carreras">
                            {   dataCarreras.moderador.map( carrera => {                    
                                    return (
                                        <Carrera key={carrera.id} data={carrera} />
                                    )
                                })
                            }
                        </div>
                        <hr />
                    </>
                    : null }

                    { dataCarreras.alumno.length > 0 ?

                    <>
                    { dataCarreras.moderador.length > 0 ? <h4 className="h4HomeSub">Carreras alumno</h4> : null}

                    <div className="carreras">
                        {   dataCarreras.alumno.map( carrera => {                    
                                return (
                                    <Carrera key={carrera.id} data={carrera} />
                                )
                            })
                        }
                    </div>
                    </>

                    : null }
                    
                </>
            :

                <div className="carreras">
                    <h1><Skeleton /></h1>

                    <ul className="anios">
                        <li className="desplegado">
                            <div className="li"><Skeleton /></div>
                            <ul className="materia">
                                <li><Skeleton /></li>
                                <li><div className="li"> <span className="txt_ma"><Skeleton /></span></div></li>
                                <li><div className="li"> <span className="txt_ma"><Skeleton /></span></div></li>
                                <li><div className="li"> <span className="txt_ma"><Skeleton /></span></div></li>
                                <li><div className="li"> <span className="txt_ma"><Skeleton /></span></div></li>
                                <li><div className="li"> <span className="txt_ma"><Skeleton /></span></div></li>
                            </ul>
                        </li>
                    </ul>

                    <ul className="anios">
                        <li className="desplegado">
                            <div className="li"><Skeleton /></div>
                            <ul className="materia">
                                <li><Skeleton /></li>
                                <li><div className="li"> <span className="txt_ma"><Skeleton /></span></div></li>
                                <li><div className="li"> <span className="txt_ma"><Skeleton /></span></div></li>
                                <li><div className="li"> <span className="txt_ma"><Skeleton /></span></div></li>
                                <li><div className="li"> <span className="txt_ma"><Skeleton /></span></div></li>
                                <li><div className="li"> <span className="txt_ma"><Skeleton /></span></div></li>
                            </ul>
                        </li>
                    </ul>

                </div>
            }
            </div>
        </div>
    );
}

export default React.memo(Home, (prevProps, nextProps) => {
    // console.log({prevProps, nextProps})
    return prevProps.dni === nextProps.dni
});
