import React from 'react';
import {stringToHslColor} from 'components/constants';
import ies21 from 'img/logo-ies-azul.svg';

export default function Avatar({dataUsuario, radius}){
    const {nombres, apellidos, initials, foto, moderador} = dataUsuario;
    const r = radius ? radius : 36;
    const mt = radius ? '0px' : '7px';
    return(
        <div className="positionRelative">
            { foto ? 
                <div>
                    {moderador ? <img src={ies21} alt="ies" className="moderador" /> : null}
                    <img alt={`${nombres}, ${apellidos}`} className="user-avatar user-avatar--image" height={r} width={r} src={foto} />
                </div>
                :
                <div className="user-avatar user-avatar--initials" style={{
                            backgroundColor: stringToHslColor(nombres + apellidos), 
                            fontSize: '15px', 
                            width: `${r}px`, 
                            height: `${r}px`, 
                            marginTop: mt
                            }}>
                    {moderador ? <img src={ies21} alt="ies" className="moderador" /> : null}
                    <div className="user-avatar__inner fx-c"><span className="user-initials">{initials}</span></div>
                </div>
            }
        </div>
    )
}